/* index.css */

@tailwind base;
@tailwind components;
@tailwind utilities;
@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@300;400;500;600;700;800;900&display=swap');
@layer base {
    body {
        @apply bg-white text-zinc-900;
    }
    .dark body {
        @apply bg-zinc-900 text-zinc-100;
    }
    a {
        @apply text-inherit;
    }
}


/* Your custom classes (no dark: here) */

.content-div {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    height: 250px;
}

.content-div:hover {
    background-image: linear-gradient( to right, rgba(112, 157, 255, 0.8), hsla(242, 74%, 61%, 0.8)) !important;
}


/* page scrolling helpers */

html {
    scroll-behavior: smooth;
    scroll-padding-top: 100px;
}